/* src/pages/NonSteriStrip/NonSteriStrip.css */
.non-steristrip-page {
  max-width: 900px;
  margin: 0 auto;
  line-height: 1.6;
}

.intro-box {
  background-color: #e8f4ff;
  border-radius: 8px;
  padding: 1.5rem;
  margin-bottom: 2rem;
  border-left: 4px solid #1e88e5;
}

.intro-box p {
  margin: 0;
  font-size: 1.1rem;
}

.disclaimer-box {
  background-color: #fff3e0;
  border-radius: 8px;
  padding: 1.5rem;
  margin-bottom: 2rem;
  border-left: 4px solid #ff9800;
}

.disclaimer-box p {
  margin: 0;
  font-size: 1rem;
  color: #bf360c;
}

/* Wound Care Steps Section */
.wound-care-steps {
  margin-bottom: 2.5rem;
}

.step-by-step {
  margin-top: 1.5rem;
}

.care-step {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid #eaeaea;
}

.care-step:last-child {
  border-bottom: none;
}

.step-number {
  background-color: #1e88e5;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 1.2rem;
  flex-shrink: 0;
}

.step-content {
  flex: 1;
}

.step-content h3 {
  color: #1565c0;
  margin-bottom: 0.8rem;
  margin-top: 0;
}

.step-content ul {
  margin-left: 1rem;
  margin-bottom: 0;
}

.step-content ul li {
  margin-bottom: 0.5rem;
}

.care-note {
  background-color: #fff8e1;
  border-left: 4px solid #ffc107;
  padding: 1rem;
  margin-top: 1rem;
  border-radius: 4px;
}

.timeline-section {
  margin-bottom: 2.5rem;
}

.timeline-categories {
  margin-top: 1.5rem;
  display: grid;
  gap: 2rem;
}

.timeline-category {
  background-color: #f8f9fa;
  border-radius: 8px;
  padding: 1.5rem;
  border-left: 4px solid #1e88e5;
}

.timeline-category h3 {
  color: #1565c0;
  margin-bottom: 0.8rem;
  margin-top: 0;
}

.timeline-note {
  font-style: italic;
  color: #666;
  margin-bottom: 1rem;
  font-size: 0.95rem;
}

.timeline {
  margin-top: 1.5rem;
}

.timeline-item {
  margin-bottom: 1.5rem;
  padding-left: 1.5rem;
  border-left: 2px solid #bbdefb;
  position: relative;
}

.timeline-item:last-child {
  margin-bottom: 0;
}

.timeline-item::before {
  content: '';
  position: absolute;
  left: -8px;
  top: 0;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: #1e88e5;
}

.timeline-item h4 {
  color: #1565c0;
  margin-bottom: 0.5rem;
  margin-top: 0;
  font-size: 1.1rem;
}

.special-considerations {
  margin-bottom: 2.5rem;
}

.consideration-item {
  margin-bottom: 1.2rem;
  padding-bottom: 1.2rem;
  border-bottom: 1px solid #eaeaea;
}

.consideration-item:last-child {
  border-bottom: none;
  padding-bottom: 0;
}

.consideration-item h3 {
  color: #1565c0;
  margin-bottom: 0.8rem;
}

.consideration-item ul {
  margin-left: 1.5rem;
}

.consideration-item ul li {
  margin-bottom: 0.8rem;
}

.warning-signs {
  margin-bottom: 2.5rem;
}

.warning-box {
  background-color: #fff4e5;
  border-radius: 8px;
  padding: 1.5rem;
  border-left: 4px solid #ff9800;
}

.warning-box ul {
  margin-left: 1.5rem;
  margin-bottom: 1rem;
}

.warning-box ul li {
  margin-bottom: 0.5rem;
}

.text-link {
  color: #1e88e5;
  font-weight: 500;
  transition: color 0.2s;
}

.text-link:hover {
  color: #0d47a1;
  text-decoration: underline;
}

.navigation-links {
  display: flex;
  justify-content: space-between;
  padding-top: 1.5rem;
  margin-top: 2rem;
  border-top: 1px solid #eaeaea;
}

.back-link, .next-link {
  color: #1e88e5;
  font-weight: 500;
  transition: color 0.2s;
}

.back-link:hover, .next-link:hover {
  color: #0d47a1;
  text-decoration: underline;
}

/* Responsive Design */
@media (max-width: 768px) {
  .care-step {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .step-number {
    align-self: flex-start;
    width: 30px;
    height: 30px;
    font-size: 1rem;
  }
  
  .navigation-links {
    flex-direction: column;
    gap: 1rem;
  }
}

@media (max-width: 480px) {
  .non-steristrip-page {
    padding: 0 1rem;
  }
}